import React from "react";
import "./css/App.css";
import profilePic from "../images/header_profile.svg";
import userDataConfig from "../config/user.json";
import marked from "marked";

// **Icons**
import emailIcon from "../images/icons/email.png";
import telIcon from "../images/icons/tel.png";
import linkedInIcon from "../images/icons/linkedin.png";
import websiteIcon from "../images/icons/website.png";
import twitterIcon from "../images/icons/twitter.png";
import checkIcon from "../images/icons/check.png";

// Download the PDF
import doScreenCapture from "./screencapture";

const RATING_SIZE = 4;

export default class Resume extends React.Component {
  constructor(props) {
    super(props);
    this.state = { userData: userDataConfig };
  }

  heroBanner = () => {
    const { userData } = this.state;
    const { name, designation, textColor = "" } = userData;

    return (
      <div className="pad-75 flex-row">
        <img src={profilePic} alt="" className="profile-pic" />
        <div className="header-text">
          <div className={`name-styles ${textColor}`}>{name}</div>
          <div className="sub-name-styles">{designation}</div>
        </div>
      </div>
    );
  };

  getMarkdownText(text) {
    var rawMarkup = marked(text, { sanitize: true });
    return { __html: rawMarkup };
  }

  iconRenderer = (icon) => {
    const { userData } = this.state;
    const { backgroundColor = "" } = userData;

    return <img src={icon} alt="" className={`icon-size ${backgroundColor}`} />;
  };

  leftSideBody = () => {
    const { userData } = this.state;
    const {
      profileBody = "",
      textColor = "",
      birthDate = "",
      address = "",
      email = "",
      phone = "",
      gitPage = "",
      linkedInPage = "",
      twitter = "",
    } = userData;

    return (
      <div className="pad-75 left-side-body">
        {/* PROFILE */}
        <div className="heading-text">Profile</div>
        <div
          className="padding-small profile-text"
          dangerouslySetInnerHTML={this.getMarkdownText(profileBody)}
        />
        {/* PERSONAL DETAILS */}
        <div className="heading-text">PERSONAL DETAILS</div>
        <div className={`sub-heading-text ${textColor}`}>Birth Date</div>
        <div
          className="padding-small profile-text"
          dangerouslySetInnerHTML={this.getMarkdownText(birthDate)}
        />
        <div className={`sub-heading-text ${textColor}`}>Address</div>
        <div
          className="padding-small profile-text"
          dangerouslySetInnerHTML={this.getMarkdownText(address)}
        />

        {/* CONTACT */}
        <div className={`sub-heading-text ${textColor}`}>CONTACT</div>
        <div className="flex-row icons-text-style">
          <div className="align-self-center max-height-20">
            {this.iconRenderer(emailIcon)}
          </div>
          <span className="margin-small border-bottom-solid-black">
            <a
              href={`mailTo:${email}`}
              style={{ textDecoration: "none", color: "inherit" }}
              rel="noopener noreferrer"
              target="_blank"
            >
              {email}
            </a>
          </span>
        </div>

        <div className="flex-row icons-text-style">
          <div className="align-self-center max-height-20">
            {this.iconRenderer(telIcon)}
          </div>
          <span className="margin-small">
            <a
              href={`tel:${phone}`}
              style={{ textDecoration: "none", color: "inherit" }}
              rel="noopener noreferrer"
              target="_blank"
            >
              {phone}
            </a>
          </span>
        </div>

        <div className="flex-row icons-text-style">
          <div className="align-self-center max-height-20">
            {this.iconRenderer(websiteIcon)}
          </div>
          <span className="margin-small border-bottom-solid-black">
            <a
              href={gitPage}
              rel="noopener noreferrer"
              target="_blank"
              style={{ textDecoration: "none", color: "inherit" }}
            >
              {gitPage}
            </a>
          </span>
        </div>

        <div className="flex-row icons-text-style">
          <div className="align-self-center max-height-20">
            {this.iconRenderer(linkedInIcon)}
          </div>
          <span className="margin-small border-bottom-solid-black">
            <a
              href={linkedInPage}
              rel="noopener noreferrer"
              target="_blank"
              style={{ textDecoration: "none", color: "inherit" }}
            >
              {linkedInPage}
            </a>
          </span>
        </div>

        <div className="flex-row icons-text-style">
          <div className="align-self-center max-height-20">
            {this.iconRenderer(twitterIcon)}
          </div>
          <span className="margin-small border-bottom-solid-black">
            <a
              href={`${twitter}`}
              rel="noopener noreferrer"
              target="_blank"
              style={{ textDecoration: "none", color: "inherit" }}
            >
              {twitter}
            </a>
          </span>
        </div>
      </div>
    );
  };

  professionalDetails = () => {
    const { userData } = this.state;
    const {
      textColor = "",
      backgroundColor = "",
      professionalExp: {
        company = "",
        designation = "",
        description = "",
        dateOfJourney = "",
      } = {},
    } = userData;
    return (
      <div className="font-family-poppins">
        <div className="heading-text">PERSONAL DETAILS</div>
        <div
          className={`sub-heading-text ${textColor} flex-row margin-top-clear`}
        >
          <div className={`dot-field ${backgroundColor}`} />
          <span className="padding-small">{company}</span>
        </div>
        <div className="flex-row space-btw padding-left-20 profile-text text-bold">
          <div>{designation}</div>
          <div>{dateOfJourney}</div>
        </div>
        <div className="padding-left-20 profile-text margin-top-small">
          {description}
        </div>
      </div>
    );
  };

  education = () => {
    const { userData } = this.state;
    const { textColor = "", backgroundColor = "", education = [] } = userData;

    return (
      <div className="font-family-poppins">
        <div className="heading-text">EDUCATION</div>
        {education
          .filter((cur) => !!cur.study)
          .map((cur, index) => {
            const { study, college, percentageNYear } = cur;
            return (
              <div>
                <div
                  className={`sub-heading-text ${textColor} flex-row margin-top-clear padding-bottom-clear`}
                >
                  <div className={`dot-field ${backgroundColor}`} />
                  <span className="padding-small">{study}</span>
                </div>
                {index < education.length - 1 && (
                  <div className="vertical-line-css" />
                )}
                <div className="flex-row space-btw padding-left-20 profile-text text-bold padding-top-small">
                  <div>{college}</div>
                  <div className="font-size-13">{percentageNYear}</div>
                </div>
              </div>
            );
          })}
      </div>
    );
  };

  renderNameRating = (name, rating) => {
    const { userData } = this.state;
    const { backgroundColor = "" } = userData;

    return (
      <div className="flex-row font-size-14">
        {new Array(rating).fill().map((_, index) => (
          <div className={`horizontal-line-css ${backgroundColor}`} />
        ))}
        {new Array(RATING_SIZE - rating).fill().map((_, index) => (
          <div
            className={`horizontal-line-css ${backgroundColor} color_base`}
          />
        ))}
        <div className="technology_name">{name}</div>
      </div>
    );
  };

  webTechnologies = () => {
    const { userData } = this.state;
    const { webTechnologies } = userData;

    return (
      <div>
        <div className="heading-text">WEB TECHNOLOGIES</div>
        {webTechnologies.map((cur) => {
          const { name, rating } = cur;
          return this.renderNameRating(name, rating);
        })}
      </div>
    );
  };

  technicalSkills = () => {
    const { userData } = this.state;
    const { technicalSkills = [] } = userData;

    return (
      <div>
        <div className="heading-text">TECHNICAL SKILLS</div>
        {technicalSkills.map((cur) => {
          const { name, rating } = cur;
          return this.renderNameRating(name, rating);
        })}
      </div>
    );
  };

  skills = () => {
    return (
      <div className="flex-row font-family-poppins space-btw">
        {this.webTechnologies()}
        {this.technicalSkills()}
      </div>
    );
  };

  renderSkill = (text) => {
    const { userData } = this.state;
    const { backgroundColor = [] } = userData;

    return (
      <div className="flex-row">
        <img
          src={checkIcon}
          alt=""
          className={`icon-size ${backgroundColor} align-self-center`}
        />
        <div
          className="padding-medium profile-text"
          dangerouslySetInnerHTML={this.getMarkdownText(text)}
        />
      </div>
    );
  };

  extraSkills = () => {
    const { userData } = this.state;
    const { extraSkills = [] } = userData;

    return (
      <div className="min-width-320">
        <div className="heading-text">EXTRA SKILLS</div>
        {extraSkills.map((cur) => {
          const { text } = cur;
          return this.renderSkill(text);
        })}
      </div>
    );
  };

  personalProjects = () => {
    const { userData } = this.state;
    const { personalProjects = [] } = userData;

    return (
      <div className="min-width-320 padding-left-15">
        <div className="heading-text">PERSONAL PROJECTS</div>
        {personalProjects.map((cur) => {
          const { text } = cur;
          return this.renderSkill(text);
        })}
      </div>
    );
  };

  extraSkillsNProjects = () => {
    return (
      <div className="flex-row font-family-poppins space-btw">
        {this.extraSkills()}
        {this.personalProjects()}
      </div>
    );
  };

  rightSideBody = () => {
    return (
      <div className="pad-75 right-side-body">
        {this.professionalDetails()}
        {this.education()}
        {this.skills()}
        {this.extraSkillsNProjects()}
      </div>
    );
  };

  renderBody = () => {
    return (
      <div className="flex-row">
        {this.leftSideBody()}
        {this.rightSideBody()}
      </div>
    );
  };

  getColouredImg = (color, bgColor) => {
    const { userData } = this.state;

    return (
      <div
        className={`circle margin-small cursor-pointer ${bgColor}`}
        onClick={() => {
          this.setState({
            userData: {
              ...userData,
              textColor: color,
              backgroundColor: bgColor,
            },
          });
          doScreenCapture();
        }}
      />
    );
  };

  render() {
    const { userData } = this.state;
    const { backgroundColor } = userData;

    return (
      <div className={`panel-out ${backgroundColor}`}>
        <div className="padding-top-20"></div>
        <div className="panel" id="pdfDownload">
          <div className="flex-row-reverse">
            {this.getColouredImg("text-color-red", "background-color-red")}
            {this.getColouredImg("text-color-green", "background-color-green")}
            {this.getColouredImg("text-color-black", "background-color-black")}
            {this.getColouredImg("text-color-blue", "background-color-blue")}
          </div>
          <div className="flex-row resume-download">{this.heroBanner()}</div>
          {this.renderBody()}
        </div>
        <div className="padding-bottom-20"></div>
      </div>
    );
  }
}
